import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';

import { ConfigurationNavRoutingModule } from './configuration.nav-routing.module'
import { ConfigurationNavComponent } from './configuration.nav.component';
import { ConfigurationDetailComponent } from '../detail/configuration.detail.component';

@NgModule({
    imports: [
        CommonModule,
        FormsModule,
        ConfigurationNavRoutingModule,
        NgbModule.forRoot()
    ],
    exports: [ConfigurationNavComponent, ConfigurationDetailComponent],
    declarations: [ConfigurationNavComponent, ConfigurationDetailComponent],
    providers: [],
})
export class ConfigurationNavModule { }



