import { ActivatedRoute } from '@angular/router';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';

import { MLevelItemsParser, MLevelsBaseItemInterface } from '@ngxsuit';
import { SYSTEM_CONFIGURATION_ITEM } from 'Admin/interfaces'
import { UISchemaService } from 'Admin/services/ui.schema.service';
import { MQService } from '@ngxsuit'
@Component({
    selector: 'store-configuration-nav',
    templateUrl: 'configuration.nav.component.html',
    styleUrls: ['configuration.nav.component.scss']
})

export class ConfigurationNavComponent implements OnInit, OnDestroy {
    title: string;
    navitems:SYSTEM_CONFIGURATION_ITEM[];
    selected_item:SYSTEM_CONFIGURATION_ITEM;
    
    private path: string;       //current request path
    private mq_sub: Subscription
    private router_sub: Subscription

    constructor(private route: ActivatedRoute, private mq:MQService, private uischema: UISchemaService) { 
        console.log(this.route.snapshot)
    }
    
    ngOnInit() {
        this.title = 'Configuration'
        this.navitems = [];

        this.mq_sub = this.mq.getMessage('ConfigurationNavComponent').subscribe(msg => {
            if (msg.type == 'ui-schema-fetched') {
                this.navitems = this.findItem(msg.load);
                console.log(this.navitems)
            }
        });

        this.router_sub = this.route.parent.url.subscribe((data) => {
            let paths: string[] = [''];
            for (const part of data) {
                paths.push(part.path);
            }

            this.path = paths.join('/');

            this.uischema.wait4UISchema('ConfigurationNavComponent');
        });
    }

    private findItem(items: SYSTEM_CONFIGURATION_ITEM[]): SYSTEM_CONFIGURATION_ITEM[] {
        for (let item of items) {
            if ( item.link === this.path) {
                return item.items;
            }
            if (item.items !== undefined) {
                let found = this.findItem(item.items);
                if (found.length > 0) {
                    return found;
                }
            }
        }
        return [];
    }

    ngOnDestroy() {
        this.mq_sub.unsubscribe();
        this.router_sub.unsubscribe();
    }
}


