import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { Headers } from '@angular/http';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { SYSTEM_CONFIGURATION_ITEM } from 'Admin/interfaces'

import { MQService, GlobalVarService } from '@ngxsuit'

interface ITEM_CONFIG_KEY_VALUES {
    [key:string]:any;
}

@Component({
    selector: 'store-configuration-detail',
    templateUrl: 'configuration.detail.component.html',
    styleUrls: ['configuration.detail.component.scss']
})

export class ConfigurationDetailComponent implements OnInit, OnChanges {
    @Input() item:SYSTEM_CONFIGURATION_ITEM;

    config_pairs:ITEM_CONFIG_KEY_VALUES = {}
    config_pairs_temp:ITEM_CONFIG_KEY_VALUES = {}
    config_keys:string[] = [];
    
    constructor(private http:HttpClient, private mq:MQService, private gv: GlobalVarService) { }

    ngOnInit() { 
        
    }

    ngOnChanges(changes:SimpleChanges) {
        this.config_keys = [];
        this.assembleConfigKeys(this.item);
        this.loadConfigValues(this.config_keys);
    }

    private assembleConfigKeys(item:SYSTEM_CONFIGURATION_ITEM) {
        if (item && item !== undefined) {
            if (item.cpath) {
                this.config_keys.push(item.cpath);
            }
    
            if (item.items) {
                for(let subitem of item.items) {
                    this.assembleConfigKeys(subitem);
                }
            }
        }
    }

    private loadConfigValues(keys:string[]) {
        console.log('url', this.gv.getString('admin_prefix') + '/rest/v1/admin/configuration');

        if (keys.length > 0) {
            this.mq.sendMessage('app-loader', {type:"", load: {loading:true} });
            console.log('url', this.gv.getString('admin_prefix') + '/rest/v1/admin/configuration');
            this.http.get(this.gv.getString('admin_prefix') + '/rest/v1/admin/configuration?keys=' + keys.join(','), 
                {   
                    withCredentials: true,
                    // headers: new HttpHeaders({
                    //     'Authorization':'Bearer test',
                    //     'Content-Type': 'application/json',
                    //     'Accept':'application/json, text/plain, */*',
                    // })
                }
            ).subscribe(data=>{
                this.config_pairs = <ITEM_CONFIG_KEY_VALUES>data;
                this.mq.sendMessage('app-loader', {type:"", load: {loading:false} });

                let expire_at:number = Math.ceil((new Date()).getTime()/1000);
                let admin =  JSON.parse(localStorage.getItem('admin_login'));
                admin.expire = expire_at + 60 * (+this.gv.getString('session_lifetime'));
                localStorage.setItem('admin_login', JSON.stringify(admin));
            });
        }
    }

    inputChanged(item:SYSTEM_CONFIGURATION_ITEM) {
        switch(item.type) {
            case 'text':
                break;
            case 'boolean':
                this.config_pairs_temp[item.cpath] = (!this.config_pairs[item.cpath] ? 1 : 0);
                break;
            case 'select':
                this.config_pairs_temp[item.cpath] = this.config_pairs[item.cpath];
                this.config_pairs[item.cpath] = '';
                break;
        }
        if (this.config_pairs[item.cpath] != this.config_pairs_temp[item.cpath]) {
            this.postConfigValues([
                {
                    key:item.cpath,
                    value:this.config_pairs_temp[item.cpath]
                }
            ]);
        }
    }

    private postConfigValues(key_values:ITEM_CONFIG_KEY_VALUES[]) {
        // key_values = 
        // [
        //     {
        //         key:key,
        //         value:this.config_pairs_temp[value]
        //     }
        // ]
        this.mq.sendMessage('app-loader', {type:"", load: {loading:true} });
        console.log('url', this.gv.getString('admin_prefix') + '/rest/v1/admin/configuration');
        this.http.post(this.gv.getString('admin_prefix') + '/rest/v1/admin/configuration', key_values).subscribe(data=>{
            let pairs = <ITEM_CONFIG_KEY_VALUES>data;
            for(let item of key_values) {
                this.config_pairs[item.key] = item.value;
            }

            let expire_at = Math.ceil((new Date()).getTime()/1000);
            let admin =  JSON.parse(localStorage.getItem('admin_login'));
            admin.expire = expire_at + 60 * (+this.gv.getString('session_lifetime'));
            localStorage.setItem('admin_login', JSON.stringify(admin));

            this.mq.sendMessage('app-loader', {type:"", load: {loading:false} });
        });
    }
}


